import axios from 'axios'
import { CART_ADD_ITEM, CART_REMOVE_ITEM, CART_SAVE_PAYMENT_METHOD, CART_SAVE_SHIPPING_ADDRESS } from '../constants/cartConstants'

export const addToCart = (id, qty) => async (dispatch, getState) => {
  const { data } = await axios.get(`/api/products/${id}`)   //ly link trang

  dispatch({   
    type: CART_ADD_ITEM,
    payload: {
      product: data._id,
      name: data.name,
      image: data.image,
      price: data.price,
      countInStock: data.countInStock,
      qty,
    },
  })    // gi gi ata
// bnh thng  lu d liu n c 2 phng php : 1 l lu vo localStorage dnh cho nhng phng thc yu cu t b nh 
// phng php th 2 l s dng redux 
  localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems))    //lu tr  trong cart/cartItems
}

export const removeFromCart = (id) => (dispatch, getState) => {
  dispatch({
    type: CART_REMOVE_ITEM,
    payload: id,  
  })

  localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems))    // chc nng remove, tuy nhin cng ch l remove khi nhn icon
                                                                                  // cn cp nht thm remove khi  thc hin mua hng
}

export const saveShippingAddress = (data) => (dispatch) => {
  dispatch({
    type: CART_SAVE_SHIPPING_ADDRESS,
    payload: data,
  })

  localStorage.setItem('shippingAddress', JSON.stringify(data))
}

export const savePaymentMethod = (data) => (dispatch) => {
  dispatch({
    type: CART_SAVE_PAYMENT_METHOD,
    payload: data,
  })

  localStorage.setItem('paymentMethod', JSON.stringify(data))
}
